            <h5 class="pt-8">
                Kujiunga na SEMINA hii tafadhali jaza taarifa zifuatazo:
            </h5>

            <a-form :model="formState" @finish="onFinish">
                <!-- Category Selection -->
                <div class="w3-row-padding">
                    <div class="w3-col s12 m6 l6">
                        <a-form-item label="Umri" name="age"
                            :rules="[{ required: true, message: 'Tafadhali jaza jina la umri!' }]">
                            <a-input type="number" v-model:value="formState.age" />
                        </a-form-item>
                    </div>
                </div>

                <!-- First Name and Last Name -->
                <div class="w3-row-padding">
