                    </div>
                </div>

                <!-- Phone Number and Gender -->
                <div class="w3-row-padding">
                    <div class="w3-col s12 m6 l6">
                        <a-form-item label="Simu" name="phoneNumber"
                            :rules="[{ required: true, message: 'Tafadhali weka namba yako ya simu!' }]">
                            <a-input v-model:value="formState.phoneNumber" />
                        </a-form-item>
                    </div>
                    <div class="w3-col s12 m6 l6">
                        <a-space wrap class="pb-6">
                            <p class="my-2" danger>Jinsia</p>
                            <a-button @click="formState.gender = 'male'"
                                :type="formState.gender === 'male' ? 'primary' : 'dashed'">M/Me</a-button>
                            <a-button @click="formState.gender = 'female'"
                                :type="formState.gender === 'female' ? 'primary' : 'dashed'"
                                :primary="formState.gender === 'female'">M/Ke</a-button>
                        </a-space>
                    </div>
                </div>

                <!-- District and Ward -->
                <div class="w3-row-padding">
                    <div class="w3-col s12 m6 l6">
                        <a-form-item label="Mkoa" name="region"
                            :rules="[{ required: true, message: 'Tafadhali jaza mkoa!' }]">
                            <a-input v-model:value="formState.region" />
                        </a-form-item>
                    </div>
                    <div class="w3-col s12 m6 l6">
                        <a-form-item label="Wilaya" name="disrict"
                            :rules="[{ required: true, message: 'Tafadhali jaza Wilaya!' }]">
                            <a-input v-model:value="formState.district" />
                        </a-form-item>
                    </div>
                </div>

                <!-- Submit Button -->
                <a-form-item class="text-center mt-12">
                    <a-button type="primary" html-type="submit">Tuma taarifa</a-button>
                </a-form-item>
            </a-form>

        </a-card>

        <!-- Ant Design Dialog -->
        <a-modal v-model:visible="dialogVisible" :title="dialogTitle" @ok="handleDialogOk">
            <p>{{ dialogMessage }}</p>
        </a-modal>
    </div>
</template>

<script>
import { defineComponent, reactive, ref } from "vue";
import { db } from "@/firebase";
import { collection, addDoc } from "firebase/firestore";
//import 'metro-ui/build/css/metro.css';

export default defineComponent({
    setup() {
        const formState = reactive({
            firstName: "",
            lastName: "",
            phoneNumber: "",
            gender: "",
            age: "",
            district: "",
            region: "",
        });

        const dialogVisible = ref(false);
        const dialogTitle = ref("");
        const dialogMessage = ref("");

        const showDialog = (title, message) => {
            dialogTitle.value = title;
            dialogMessage.value = message;
            dialogVisible.value = true;
        };

        const handleDialogOk = () => {
            dialogVisible.value = false;
        };

        const onFinish = async (values) => {
            if (formState.gender === '') {
                showDialog(`Bainisha jinsia tafadhali!`);
            } else {
                try {
                    const submissionData = {
                        ...values,
                        submissionDate: new Date().toISOString(),
                    };
                    const docRef = await addDoc(collection(db, "applications"), submissionData);
                    console.log("Document written with ID: ", docRef.id);
                    showDialog("Success", `Ahsante sana ${formState.firstName} umefanikiwa kutuma taarifa!`);
                    // Reset the form state
                    formState.firstName = "";
                    formState.lastName = "";
                    formState.phoneNumber = "";
                    formState.gender = "";
                    formState.age = "";
                    formState.district = "";
                    formState.region = "";
                } catch (error) {
                    console.error("Error adding document: ", error);
                    showDialog("Error", "Failed to submit form. Please try again.");
                }
            }

        };

        const options = [
            { value: 'ujasiriamali', label: 'Ujasiriamali na Usimamizi wa Biashara' },
            { value: 'kiufundi', label: 'Stadi za Kiufundi na Ufundi' },
            { value: 'kilimo', label: 'Kilimo na Ufugaji' },
            { value: 'ict', label: 'Teknolojia ya Habari na Mawasiliano (ICT)' },
            { value: 'fedha', label: 'Usimamizi wa Fedha na Uwekezaji' },
        ];

        const filterOption = (input, option) =>
            option.label.toLowerCase().indexOf(input.toLowerCase()) >= 0;

        return {
            formState,
            onFinish,
            dialogVisible,
            dialogTitle,
            dialogMessage,
            handleDialogOk,
            options,
            filterOption,
        };
    },
});
</script>



